@import url(https://fonts.googleapis.com/css?family=Montserrat);
body {
  background: url("https://i.kinja-img.com/gawker-media/image/upload/m6ztwkawvypgrnn9k5o3.jpg") no-repeat center center fixed;
  background-size: cover;
  font-family: "Montserrat", sans-serif; }

.App {
  text-align: center;
  align: center; }

.App-header {
  background-color: #282c34;
  min-height: 50%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  padding-bottom: 10px; }

.container {
  width: 550px;
  margin: 80px auto; }

.leadheader {
  width: 100%;
  height: 70px;
  background: rgba(0, 168, 232, 0.79);
  text-align: center;
  border-radius: 5px 5px 0 0; }
  .leadheader h2 {
    margin: 0;
    color: #fefefe;
    padding-top: 20px; }

.colheader {
  background: rgba(254, 254, 254, 0.9);
  width: 100%;
  margin-left: 0;
  text-align: center;
  border-bottom: 1px solid rgba(0, 0, 0, 0.2); }
  .colheader h4 {
    font-size: 15px; }
  .colheader .alltime {
    cursor: pointer;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none; }
  .colheader .recent {
    cursor: pointer;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none; }

.users {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-flex-wrap: nowrap;
          flex-wrap: nowrap;
  -webkit-align-items: center;
          align-items: center;
  background: #fefefe;
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 5px;
  text-align: center; }
  .users .name {
    text-align: left; }
    .users .name a {
      padding-top: 15px;
      vertical-align: middle; }
  .users img {
    float: left;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    border: 2px solid rgba(0, 168, 232, 0.79);
    margin-right: 10px; }
  .users .rank {
    padding-left: 6px; }
  .users:nth-of-type(odd) {
    background: rgba(254, 254, 254, 0.9); }
  .users:nth-of-type(even) {
    background: rgba(240, 240, 240, 0.9); }
  .users:last-child {
    border-radius: 0 0 5px 5px; }

@media (max-width: 550px) {
  .users .name {
    padding-left: 5px; }
  .container {
    width: 100%; } }

@media (max-width: 540px) {
  .container {
    padding: 0; }
  .colheader div[class^='col-xs'] {
    padding: 0; }
  .users {
    text-align: center; }
    .users img {
      display: block;
      float: none;
      margin: 0 auto;
      width: 35px;
      height: 35px; }
    .users .name {
      text-align: center; }
      .users .name a {
        display: block;
        padding-top: 4px; } }

